export default {
	data() {
		return {
			num: '' //记录批次号
		}
	},

	methods: {
		// 跳转扫码页
		jump() {
			if (this.num == '') {
				// 未输入提醒输入
				uni.showToast({
					title: '请输入批次号',
					// icon: 'none'
					// icon:'loading'
				});
			} else {
				// 记录批次号节点
				uni.setStorage({
					key: 'num_',
					data: this.num,
				})
				console.log(this.num)
				// 跳转扫码页
				uni.navigateTo({
					url: '/pages/city/city'
				})
			}



		}
		
		
	}
}

